.Input_{
    background: transparent;
    color: white;
    outline: none;
    border: none;
    border-bottom: 1px solid white;
    padding: 5px;
    margin-right: 5px;
    font-size: 1.5rem;
    text-align: center;
}

.Input_:focus, .Input_:active{
    outline: none;
    border: none;
    border-bottom: 1px solid white;
    
}

.Input_+input{
    font-size: 1.5rem;
    background-color: yellow;
    border-radius: 10px;
    margin: 10px;
    box-sizing: border-box;
    outline: none;
}

.Form{
    grid-area: 1/1/2/2;
}

.Main_{
    display: grid;
    height: 100%;
    width: 100%;
    grid-template-columns: 100%;
    grid-template-rows: 15% 85%;
}

.Weather{
    grid-area: 2/1/3/2;
    overflow-x: scroll;
    width: 95%;
    height: 99%;
    margin: 0 auto;
    scrollbar-width: thin; 
}

*::-webkit-scrollbar{
  height: 10px;
  
}

*::-webkit-scrollbar-track {
    
  }
  
  *::-webkit-scrollbar-thumb {
    background-color: green;
    border-radius: 20px;
    border: 3px solid yellow;
    
  }
